"use client"

import { useState } from 'react'
import { useRouter } from 'next/navigation'
import Link from 'next/link'
import { Button } from '@/components/ui/button'
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Chrome, Loader2 } from 'lucide-react'
import { signUpEmailPassword, signInWithGoogle } from '@/lib/firebase/auth'
import { ADMIN_PASSKEY } from '@/lib/config/admin'
import { useToast } from '@/hooks/use-toast'

export default function AdminSignupPage() {
  const router = useRouter()
  const { toast } = useToast()
  const [loading, setLoading] = useState(false)
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password: '',
    passkey: '',
  })

  const handleEmailAuth = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)

    // Verify passkey
    if (formData.passkey !== ADMIN_PASSKEY) {
      toast({
        title: 'Invalid Passkey',
        description: 'The admin passkey is incorrect.',
        variant: 'destructive',
      })
      setLoading(false)
      return
    }

    try {
      const { user, error } = await signUpEmailPassword(
        formData.email,
        formData.password,
        formData.name,
        'admin' // Set role as admin
      )
      if (error) {
        toast({
          title: 'Error',
          description: error,
          variant: 'destructive',
        })
      } else if (user) {
        toast({
          title: 'Success',
          description: 'Admin account created successfully!',
        })
        router.push('/admin/dashboard')
        router.refresh()
      }
    } catch (error: any) {
      toast({
        title: 'Error',
        description: error.message || 'An error occurred',
        variant: 'destructive',
      })
    } finally {
      setLoading(false)
    }
  }

  const handleGoogleAuth = async () => {
    // Verify passkey first
    if (formData.passkey !== ADMIN_PASSKEY) {
      toast({
        title: 'Invalid Passkey',
        description: 'Please enter the admin passkey first.',
        variant: 'destructive',
      })
      return
    }

    setLoading(true)
    try {
      const { user, error } = await signInWithGoogle('admin')
      if (error) {
        toast({
          title: 'Sign-in Error',
          description: error,
          variant: 'destructive',
        })
        setLoading(false)
        return
      }
      
      if (user) {
        toast({
          title: 'Success',
          description: 'Admin account created with Google!',
        })
        router.push('/admin/dashboard')
        router.refresh()
      }
    } catch (error: any) {
      console.error("Google auth error:", error)
      toast({
        title: 'Error',
        description: error.message || 'An unexpected error occurred. Please try again.',
        variant: 'destructive',
      })
    } finally {
      setLoading(false)
    }
  }

  return (
    <main className='flex-col flex justify-center items-center min-h-screen bg-muted/30'>
      <div className='container py-20'>
        <div className='mx-auto max-w-md'>
          <Card className='border-border bg-card shadow-sm'>
            <CardHeader className='space-y-1'>
              <CardTitle className='text-2xl font-bold'>Create Admin Account</CardTitle>
              <CardDescription>Set up your admin account for Grad Drive</CardDescription>
            </CardHeader>
            <form onSubmit={handleEmailAuth}>
              <CardContent className='space-y-4'>
                <div className='space-y-2'>
                  <Label htmlFor='name'>Full Name</Label>
                  <Input
                    id='name'
                    type='text'
                    placeholder='John Doe'
                    value={formData.name}
                    onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                    required
                  />
                </div>
                <div className='space-y-2'>
                  <Label htmlFor='email'>Email</Label>
                  <Input
                    id='email'
                    type='email'
                    placeholder='admin@example.com'
                    value={formData.email}
                    onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                    required
                  />
                </div>
                <div className='space-y-2'>
                  <Label htmlFor='password'>Password</Label>
                  <Input
                    id='password'
                    type='password'
                    placeholder='••••••••'
                    value={formData.password}
                    onChange={(e) => setFormData({ ...formData, password: e.target.value })}
                    required
                    minLength={6}
                  />
                </div>
                <div className='space-y-2'>
                  <Label htmlFor='passkey'>Admin Passkey</Label>
                  <Input
                    id='passkey'
                    type='password'
                    placeholder='Enter admin passkey'
                    value={formData.passkey}
                    onChange={(e) => setFormData({ ...formData, passkey: e.target.value })}
                    required
                  />
                  <p className='text-xs text-muted-foreground'>
                    Required for admin account creation
                  </p>
                </div>
                <Button className='w-full' size='lg' type='submit' disabled={loading}>
                  {loading ? (
                    <>
                      <Loader2 className='mr-2 h-4 w-4 animate-spin' />
                      Creating account...
                    </>
                  ) : (
                    'Create Admin Account'
                  )}
                </Button>
                <div className='relative'>
                  <div className='absolute inset-0 flex items-center'>
                    <span className='w-full border-t' />
                  </div>
                  <div className='relative flex justify-center text-xs uppercase'>
                    <span className='bg-card px-2 text-muted-foreground'>Or continue with</span>
                  </div>
                </div>
                <Button
                  className='w-full'
                  size='lg'
                  variant='outline'
                  type='button'
                  onClick={handleGoogleAuth}
                  disabled={loading}
                >
                  <Chrome className='mr-2 h-4 w-4' />
                  Sign up with Google
                </Button>
              </CardContent>
            </form>
            <CardFooter className='flex flex-col space-y-4'>
              <div className='text-sm text-muted-foreground text-center'>
                Already have an admin account?{' '}
                <Link href='/admin/login' className='text-accent hover:underline font-medium'>
                  Sign in
                </Link>
              </div>
              <div className='text-sm text-muted-foreground text-center'>
                <Link href='/admin' className='text-accent hover:underline'>
                  ← Back to admin portal
                </Link>
              </div>
            </CardFooter>
          </Card>
        </div>
      </div>
    </main>
  )
}

